.tabs {
  &__title {
    text-align: center;
    margin-bottom: 50px; } }
.service {
  padding: 5rem; }
.service-header {
  border: 1px solid #db4453;
  border-radius: 3rem;
  margin-bottom: 3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between; }
.service-header-tab {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  height: auto;
  text-transform: uppercase;
  vertical-align: middle;
  padding: .5rem 1rem;
  cursor: pointer;
  margin: 0;
  font-weight: 400;
  color: #db4453;
  font-size: 1.2rem;
  text-align: center; }
.service-header-tab.active {
  background-color: #db4453;
  border-radius: 3rem;
  color: #fff !important; }
.service-header-tab span {
  font-weight: 700; }
.service .description {
  font-weight: 300;
  color: #19b5fe; }
.service .service-outer {
  text-align: left; }
.service-inner {
  text-align: justify; }
.service img {
  width: 100%; }
